

*{
  box-sizing: border-box;
  padding: 0;
  margin:0;
font-family: 'Hind Madurai', sans-serif;

}

/*scroll-bar*/
::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 15px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #4B4040; 
  border-radius: 15px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #A51F42; 
}
.app{
  overflow-y: scroll;
  height: 100vh;
  scroll-snap-type: y mandatory;
}

.container{
  display: flex;
height: 100vh;
flex-direction: column;
align-items:center;
justify-content: space-around;
scroll-snap-align:start;
padding-top: 10px;

}
.landing-info{
  display: flex;
  flex-direction: column;
 justify-content: first baseline;
  flex-grow: 2;
padding-top: 100px;

 
}
.head{
  font-size: 50px;
  text-align: center;
}
.landing-para{
  font-size: larger;
  text-align: center;

}
a{
  text-decoration: underline;
  color: #3E4145;
}
a:hover{
  text-decoration:underline;
 text-decoration-thickness: 2px;
}
.downarow{

border: none;
cursor:pointer;
  background-color: transparent;
  animation-name: example;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}
.downarowimg{

  width: 20px;
  height: 40px;
opacity: 0.5;

}
.buttons{
display: flex;
flex-wrap: no-wrap;
gap: 2rem;
padding-bottom: 50px;

}
.b1{
  width: 12rem;
  height: 40px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  font-size: large;
  font-weight: bolder;
  background-color: #3E4145;
  color:white;
  word-spacing: 0.3em;
 text-align: center;
}
.b2{
  width: 12rem;
  height: 40px;
  font-size: large;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  font-weight: bolder;
  word-spacing: 0.2em;
  padding: 5px;
  text-align: center;
  background-color: #E6E7E5;
  color: #3E4145;
}
.tesla-logo{
  text-align: center;
}
/*Keyframes for down button*/
@keyframes example {
  0%   {transform: translateY(1rem);}
  25%  {transform: translateY(0px);}
  50%  {transform: translateY(1rem);}
  75%  {transform: translateY(0px);}
  100% {transform: translateY(1rem);}
}
.solar-para{
font-size:large;
color: #493e3e;
text-align: center;
}

/*Navbar*/
.nav-container{
  display:flex;
  align-items: flex-start;
  justify-content: space-evenly;
  text-align: center;
  padding-top: 20px;
position:absolute;
width: 100%;
font-weight: bold;

}


.nav-items:hover{
background-color: #98B1C6;
cursor: pointer;

}

/*Footer*/
.footer-container{
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  gap:1.5rem;
  margin-bottom: 5px;
}
.footer-childern{
color:#585A5E;
text-align: justify;
white-space: nowrap; 
}
.footer-anchor-tag{
  text-decoration: none;

}
.footer-anchor-tag:hover{
text-decoration: underline;
text-decoration-thickness: 3px;

}
@media only screen and (max-width: 907px) {
.app{
  background-size: 100vh;
}
  .buttons{
    gap: 15px;
    flex-direction: column;
  }
  .b1{
    width: 200px;
    height: 45px;
    word-spacing: 0em;
  }
  .b2{
    word-spacing: 0em;
    width:200px;
    height: 45px;
  }
  .nav-items{

    display:none;
  }
}
@media only screen and (max-width: 454px) {
  .footer-container{
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
}
